// eslint-disable-next-line import/extensions
import buttons from './buttons.js';

export default function createSetting() {
  const PLACE = document.createElement('div');
  PLACE.className = 'container';
  document.body.append(PLACE);

  const HEADER = document.createElement('h1');
  HEADER.innerHTML = 'Virtual MacOS keyboard';
  PLACE.append(HEADER);

  const TEXT_AREA = document.createElement('textarea');
  // TEXT_AREA.setAttribute('disabled', 'true');
  PLACE.append(TEXT_AREA);

  const KEYBOARD = document.createElement('div');
  KEYBOARD.className = 'keyboard';
  PLACE.append(KEYBOARD);

  // eslint-disable-next-line no-plusplus
  function addBtnsToRow(rowNum) {
    // eslint-disable-next-line no-restricted-syntax
    for (const button of buttons[`row_${rowNum}`]) {
      const btn = document.createElement('button');
      btn.className = 'button';
      if (button.type === 'symbol') {
        btn.classList.add('button_symbol');
        btn.innerHTML = button.en;
      } else {
        btn.classList.add('button_modifier');
        btn.innerHTML = button.symbol;
      }
      btn.id = button.btnName;

      document.querySelectorAll('.keyboard__row')[rowNum].append(btn);
    }
  }

  // eslint-disable-next-line no-plusplus,guard-for-in,no-restricted-syntax
  for (const rw in buttons) {
    const row = document.createElement('div');
    row.className = 'keyboard__row';
    row.id = `${rw}`;
    KEYBOARD.append(row);
    addBtnsToRow(rw.slice(-1));
  }

  document.querySelector('#Backspace').classList.add('button_size_medium');
  document.querySelector('#Tab').classList.add('button_size_medium');
  document.querySelector('#CapsLock').classList.add('button_size_long');
  document.querySelector('#Enter').classList.add('button_size_long');
  document.querySelector('#ShiftLeft').classList.add('button_size_medium');
  document.querySelector('#ArrowUp').classList.add('button_size_small');
  document.querySelector('#ShiftRight').classList.add('button_size_small');
  document.querySelector('#Language').classList.add('button_size_small');
  document.querySelector('#ControlLeft').classList.add('button_size_small');
  document.querySelector('#AltLeft').classList.add('button_size_small');
  document.querySelector('#MetaLeft').classList.add('button_size_medium');
  document.querySelector('#Space').classList.add('button_space');
  document.querySelector('#MetaRight').classList.add('button_size_medium');
  document.querySelector('#AltRight').classList.add('button_size_small');
  document.querySelector('#ArrowLeft').classList.add('button_size_small');
  document.querySelector('#ArrowDown').classList.add('button_size_small');
  document.querySelector('#ArrowRight').classList.add('button_size_small');
}
